<!-- 고객 사용 페이지 > 문의 목록 조회 페이지 -->
<template>
  <h1>고객 사용 페이지 > 문의 목록 조회 페이지</h1>

  <!-- 검색조건 -->
  <div>
    <div class="searchbox">
      <span>
        <select v-model="searchbox.condition">
          <option v-for="data in searchbox.conditions" :value="data.value">{{data.text}}</option>
        </select>
      </span>
      <span><input v-model="searchbox.text"/></span>
      <button @click="fn_search">조회</button>
    </div>
    <div class="action-buttons">
      <button router-link to="/inquiryWrite" >문의등록</button>
    </div>
  </div>
  
  <div>
    <table>
      <colgroup>
        <col width="5%">
        <col width="20%">
        <col width="25%">
        <col width="25%">
        <col width="10%">
        <col width="15%">
        <col width="15%">
      </colgroup>
      <thead>
        <tr>
          <th>번호</th>
          <th>문의상태</th>
          <th>제목</th>
          <th>문의내용</th>
          <th>답변내용</th>
          <th>사용자ID</th>
          <th>문의일시</th>
        </tr>
      </thead>
    <tbody>
      <tr v-for="data in inquiries">
        <td>{{data.inquirySeq}}</td>
        <td><span >{{data.status}}</span></td>
        <td>{{data.title}}</td>
        <td>{{data.inquiryContent}}</td>
        <td>{{data.answerContent}}</td>
        <td>{{data.inquiryUserId}}</td>
        <td>{{data.inquiryDate}}</td>
      </tr>
    </tbody>
    </table>
  </div>

</template>

<style scoped>
table {
    width: 100%;
    border: 1px solid gray;
    border-collapse: collapse;
  }
  th, td {
    border: 1px solid gray;
    padding: 10px;
  }
  .searchbox {
    float: left;
  }
  .action-buttons {
    float: right;
  }
  .span.status001 {
    background-color: yello;
    padding: 4px;
  }
  .span.status002 {
    background-color: blue;
    padding: 4px;
  }
  .span.status003 {
    background-color: green;
    padding: 4px;
  }
</style>

<script>

export default {
  name: 'BoardList',
  data: function () {
    return {
      inquiries: [],
      isModalViewed: false,
      searchbox : { 
        condition : '',
        conditions : [
          {value: "", text: "전체"},
          {value: "inquiryUserId", text: "사용자ID"},
          {value: "title", text: "제목"},
        ],
        text : '',
      },
      param : '',
    }
  },
  created: function () {
    console.log("created");
    this.read()
  },
  methods: {
    fn_search() {
      if(this.searchbox.text) {
        if(this.searchbox.condition) {
          this.param = this.searchbox.condition + ":*" + this.searchbox.text + "*";
        }else {
          this.searchbox.conditions.forEach((data, index) => {
              if(data.value) {
                if(this.param) {
                  this.param += " OR ";
                }
                console.log('Index: ' + index + ' Value: ' + data.value);
                this.param += data.value + ":*" + this.searchbox.text + "*";
              }
          });
        }
      }
      this.read();
    },
    read() {
      const url = "/api/v1/inquiries"
      this.axios.get(url, {
        params: {
          search: this.param
        }
      }).then(response => {
        console.log('### response: ' + JSON.stringify(response));
        this.inquiries = response.data.content;
      }).catch(error => {
        console.log(error);
      }).finally(() => {
        this.param = "";
      });
    }
  }
}

</script>