<!-- 상담사 사용 페이지 > 답변 작성 페이지 -->
<template>
  <h1>상담사 사용 페이지 > 답변 작성 페이지</h1>
  <form @submit.prevent="fn_submit">
  <div>
      <table>
        <colgroup>
          <col width="10%">
          <col width="40%">
          <col width="10%">
          <col width="40%">
        </colgroup>
      <tbody>
        <tr>
          <td>제목</td>
          <td colspan="3">{{form.title}}</td>
        </tr>
        <tr>
          <td>사용자ID</td>
          <td>{{form.inquiryUserId}}</td>
          <td>문의일시</td>
          <td>{{form.inquiryDate}}</td>
        </tr>
        <tr>
          <td>문의내용</td>
          <td colspan="3">{{form.inquiryContent}}</td>
        </tr>
        <tr>
          <td>상담사ID</td>
          <td colspan="3">{{form.answerUserId}}</td>
        </tr>
        <tr>
          <td>답변내용</td>
          <td colspan="3"><textarea v-model="form.answerContent" style="width:100%"/></td>
        </tr>
      </tbody>
      </table>
    
  </div>
  <div>
    <button type="submit">등록</button>
    <button>취소</button>
  </div>
  </form>

</template>

<style lang="scss">
table {
    width: 100%;
    border: 1px solid gray;
    border-collapse: collapse;
  }
  th, td {
    border: 1px solid gray;
    padding: 10px;
  }
  input, textarea {
    float: left;
  }

</style>

<script>
export default {
  name: 'BoardList',
  data: function () {
    return {
      form : {
        title : '',
        inquiryUserId : '',
        inquiryContent : '',
        answerUserId : '',
        answerContent : '',
      },
      errors: [],
      errorshow: false,
    }
  },
  created: function () {
  },
  methods: {
    fn_validate() {
      console.log("fn_validate");
      this.errors = [];
      if(!this.form.title) {
        this.errors.push("제목을 입력해주세요.");
      }
      if(!this.form.inquiryUserId) {
        this.errors.push("사용자ID를 입력해주세요.");
      }
      if(!this.form.inquiryContent) {
        this.errors.push("문의내용을 입력해주세요.");
      }
      if(!this.errorshow) {
        return true;
      };
    },
    fn_submit() {
      console.log("fn_submit");
      this.fn_validate();
      if(this.errors.length == 0) {
        if(confirm("문의를 등록하시겠습니까?")) {
          this.create();
        }
      }
    },
    create() {
      console.log("create");
      const url = "/api/v1/inquiries"
      this.axios.put(url, this.form)
      .then(response => {
        console.log('### response: ' + JSON.stringify(response));
        this.inquiries = response.data.content;
      }).catch(error => {
        console.log(error);
      }).finally(() => {

      });
    }
  }
}

</script>
